@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
    --color-black: #221815;
    --color-red: #E60012;
    --color-red-1: #c30d23;
}

body {
    min-width: 1920px;
    color:var(--color-black);
    line-height: 1.2;
}

.app-layout {
}
// #app {
//     min-width: min-content;
// }
.app-header {
    margin: 0 auto;
    width: 1655px;
    height: 140px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    .logo {
        flex-shrink: 0;
        margin-right: 35px;
        width: 95px;
        height: 59 + 8 + 22 + 5 + 5px;

    }
    .name {
        border-left: 1px solid var(--color-black);
        padding: 0 40px 0 17px;
        height: 74px;
        flex-shrink: 0;
        h2 {
            font-family: MicrosoftYaHei;
            font-size: 33px;
            color:var(--color-red);
            line-height: 1;
        }
        p {
            margin-top: 17px;
            font-size: 17px;
            font-family: SimHei;
            font-weight: 400;
            color: #231815;
        }
    }

    .tags {
        border-left: 1px dashed #a0a0a1;

        .tag {
            height: 84px;
            border-right: 1px dashed #a0a0a1;
            gap: 7px;
            justify-content: flex-end;
            width: 150px;
            align-items: center;
            img {
                width: 45px;
                height: 45px;
                display: block;
            }
            span {
                font-size: 16px;
                font-family: SimHei;
                font-weight: 400;
                color: #231815;
            }
        }
    }

    .qr {
        margin-left: 113px;
        width: 106px;
        flex-shrink: 0;
        img {
            margin-bottom: 7px;
            width: 106px;
            height: 106px;
            box-shadow: 0 0 10px 0 #666565 ;
        }
        p {
            font-size: 12px;
            font-family: SimHei;
            font-weight: 400;
            color: #231815;
            zoom: calc(7/12);
            white-space: nowrap;
            text-align: center;
        }
    }

    .contact {
        flex-shrink: 0;
        margin-left: 32px;
        display: flex;
        flex-direction: column;
        line-height: 1;
        p {
            font-size: 14px;
            font-family: SimHei;
            font-weight: 400;
            color: #727171;
        }
        h2 {
            margin-top: 9px;
            font-size: 27px;
            font-family: MicrosoftYaHei;
            font-weight: bold;
            color: #231815;
        }
        h3 {
            margin-top: 12px;
            font-size: 24px;
            font-family: MicrosoftYaHei;
            font-weight: bold;
            color: var(--color-red);
        }
        span {
            margin-top: 10px;
            padding: 7px;
            font-size: 11px;
            font-family: SimHei;
            font-weight: 400;
            color: #fff;
            background-color: var(--color-red);
            border-radius: 24px;

        }
    }
}


.app-nav {
    min-width: 1280px;
    display: flex;
    align-items: stretch;
    height: 56px;
    background-color: #474748;
    .nav {
        position: relative;
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 20px;
        font-family: SimHei;
        color:#fff;
        &:hover,
        &.on {
            background-color: var(--color-red);
        }
        &.on {
            &::after {
                content: '';
                position: absolute;
                bottom: -15px;
                width: 0;
                height: 0;
                border: 15px solid;
                border-color: var(--color-red) transparent transparent transparent;
                border-bottom: none;
            }
        }
    }
}

.app-banner {
    width: 100%;
    aspect-ratio: calc(4000/1293);
    img {
        object-fit: cover;
    }
}

.app-feature {
    margin-top: 65px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 45px;
    .title {
        font-size: 48px;
        em {
            color:var(--color-red);
            font-style: normal;
        }
    }
    .subtit {
        margin-top: 15px;
        font-size: 22px;
    }
    .features {
        margin-top: 63px;
        display: flex;
        width: 100%;
        gap: 20px;
        align-items: center;
        justify-content: center;
    }
    .feature {
        display: flex;
        flex-direction: column;
        align-items: center;

        &:hover,
        &.on {
            .content {
                background-color:var(--color-red-1);
                color:#fff;
            }
        }

        .icon {
            transform: translateY(23px);
            background-color:#fff;
            width: 132px;
            height: 132px;
            display: flex;
            align-items: center;
            justify-content: center;
            border: 1px solid var(--color-red);
            border-radius: 100%;

            img {
                transform: scale(.5);
            }
        }
        .content {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 30px;
            width: 314px;
            height: 233px;
            background-color: #efeff0;
            border-radius: 24px;
            padding: 55px 30px 10px;
            font-size: 20px;
            line-height: 1.4;
            color:var(--color-black);
            transition: all .2s ease-in-out;

            .tit {
               font-size: 30px;
               font-weight: bold; 
            }
        }
    }
}

.app-efficiency {
    display: flex;
    align-items: center;
    justify-content: center;
    aspect-ratio: 3.2;
    background-image: url(../source/img/efficiency_bg.jpg);
    gap: 97px;
    .content {
        padding: 71px 44px 44px;
        width: 1172px;
        height: 339px;
        font-size: 23px;
        line-height: 38px;
        background-color:rgba(255,255,255,.72);
        flex-shrink: 0;

        .tit {
            text-align: center;
            margin-bottom: 38px;
            font-size: 58px;
            line-height: 1.2;
            color:#036eb8
        }
    }
    .right {
        width: 496px;
        height: 339px;
        object-fit: cover;
    }
}

.app-product {
    position: relative;
    padding: 109px 20px 80px;
    text-align: center;

    .bg-mask {
        position: absolute;
        z-index: -1;
        top: 36px;
        left: 0;
        right: 0;
        margin: auto;
        font-size: 155px;
        font-weight: bold;
        color:#dcdddd;
        opacity: .32;

    }

    .tit {
        font-weight: bold;
        font-size: 69px;
    }

    .subtit {
        margin-top: 29px;
        font-size: 29px;
    }

    .tabs {
        margin-top: 62px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .tab {
        position:relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-end;
        gap: 30px;
        width: 265px;
        height: 138px;
        font-size: 33px;
        overflow: hidden;

        &.on , &:hover {
            color:var(--color-red);
            .icon {
                filter: invert(14%) sepia(79%) saturate(4574%) hue-rotate(344deg) brightness(87%) contrast(102%);
            }
        }

        .icon {
            width: 82px;
            filter: invert(0%) sepia(14%) saturate(14%) hue-rotate(346deg) brightness(98%) contrast(86%);
        }

        & + .tab {
            &::before {
                content: '';
                position: absolute;
                left: 0;
                top: 0;
                bottom: 0; 
                width: 1px;
                height: 130px;
                margin: auto;
                background-color: var(--color-black);
            }
        }
    }

    .tab-content {
        margin-top: 64px;
        position: relative;
        overflow: hidden;

        .content {
            display: grid;
            align-items: center;
            justify-content: center;
            grid-template-columns: repeat(3, 517px);
            flex-direction: wrap;
            row-gap: 85px;
            column-gap: 60px;
        }

        .product {
            position: relative;
            display: flex;
            width: 517px;
            height: 350px;
            overflow: hidden;
            &:hover {
                img {
                    transform: scale(1.2);
                }
                span {
                    background-color: var(--color-red);
                }
            }
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                transition: transform .2s ease-in-out;
            }
            span { 
                 position: absolute;
                z-index: 1;
                left: 0;
                bottom: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 100%; 
                height: 60px;
                color:#fff;
                font-size: 28px;
                background-color:rgba(0,0,0, .46);
                transition: background-color .2s ease-in-out;
            }
        }
    }
}

.app-why {
    padding: 50px 20px 70px;
    height: 678px;
    background-color: rgba(0,0,0,.52);
    background-image: url(../source/img/02_02.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    text-align: center;
    color:#fff;
    display: flex;
    flex-direction: column;
    .tit {
        font-size: 54px;
        font-weight: bold;
    }
    .subtit {
        margin-top: 20px;
        font-size: 23px;
    }

    .cards {
        margin-top: auto;
