.app-plan {
    padding: 92px 10px 76px; 
    display: flex;
    flex-direction: column;
    align-items: center;
    .tit {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 19px;
        font-weight: bold;
        font-size: 49px;
        &::after,
        &::before {
            content: '';
            width: 122px;
            height: 1px;
            background-color: var(--color-black);
        }
    }

    .subtit {
        margin-top: 20px;
        font-size: 28px;
    }

    .cover {
        margin-top: 62px;
        width: 1500px;
        height: 746px;
        background-color:#beac9a;
    }

    .navs {
        margin-top: 66px;
        display: flex;
        align-items: center;

        .nav {
            position: relative;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            flex-shrink: 0;
            width: 250px;
            height: 100px;
            gap: 10px;
            transition: all .2s ease-in-out;

            &:nth-of-type(odd) {
                background-color:#e8e7e6;
            }
            &:nth-of-type(even) {
                background-color:#f4f3f3;
            }

            &::after {
                content: '';
                position: absolute;
                top: -15px;
                width: 0;
                height: 0;
                border: 15px solid;
                border-color:  transparent transparent var(--color-red) transparent ;
                border-top: none;
                opacity: 0;
                transition: opacity .2s ease-in-out;
            }

            &:hover, 
            &.on {
                color:#fff;
                background-color: var(--color-red);
                &::after {
                    opacity: 1;
                }
            }
            

            p {
                font-size: 20px;
            }
            span {
                font-size: 17px;
            }
        }
    }
}

.app-point {
    padding: 44px 10px 38px;
    color:#fff;
